	export default{
			data(){
				return{
					isPassword:true,
					isPassword1:true,
					checked:true,
					pass:'',
					pwd1:'',
					pwd2:''
				}
			},
            onBackPress(option){
                  plus.key.hideSoftKeybord()    
                },
			methods:{
				getNowPwd:function(e){
					this.pass=e.detail.value;
				},
				getPwd1:function(e){
					this.pwd1=e.detail.value;
				},
				getPwd2:function(e){
					this.pwd2=e.detail.value;
				},
				show:function(){
					this.isPassword=!this.isPassword
				},
				show1:function(){
					this.isPassword1=!this.isPassword1
				},
				other:function(){
					uni.navigateTo({
						url:'../login-password/login-password'
					})
				},
				save:function(){
					var _self=this;
					if(this.pass==""){
						uni.showToast({
						  title: '请输入现有密码',
						  icon: 'none',
						  duration: 2000
						})
						return false
					}
					if(this.pwd1==""){
						uni.showToast({
						  title: '请输入登录密码',
						  icon: 'none',
						  duration: 2000
						})
						return false
					}
					var str =/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
					if(!str.test(this.pwd1)){
						uni.showToast({
							title:'密码格式不正确',
							icon:'none',
							duration:2000
						})
						return false
					}
					if(this.pwd2==""){
						uni.showToast({
						  title: '请确认登录密码',
						  icon: 'none',
						  duration: 2000
						})
						return false
					}
					if(this.pwd1!==this.pwd2){
						uni.showToast({
							title:'两次密码不一致',
							icon:'none',
							duration:2000
						})
						return false
					}
					uni.request({
						url:this.url+'updataLoginpassword/',
						method: 'POST',
						data: {
						    mobile:this.global_.phone,
							password:this.pass,
							password1:this.pwd1,
							password2:this.pwd2
						},
						headers: {
						   Authorization:'JWT'+' '+this.global_.token
						},
						success: res => {
							if(res.statusCode==200){
								uni.showToast({
									title:'登陆密码修改成功',
									icon:'none',
									duration:2000
								})
								uni.removeStorageSync('phone')
								uni.removeStorageSync('token')
								uni.reLaunch({
									url:'../../pages/login/login'
								})
							}
							if(res.statusCode==400){
								uni.showToast({
									title:'现有密码错误',
									icon:'none'
								})
							}
						},
					    fail: () => {},
						complete: () => {}
					});
				}
			}
		}
